import { Injectable } from '@angular/core';
import {environment} from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class EndUseService {
  apiUrl2 = `${environment.API_URL2}/chargingStation/` ;
  apiUrl3 = `${environment.API_URL2}/usermanagement` ;
  constructor(private http : HttpClient) { }

  getChargingStationsByUserRoleAndLatLongUW(login_id,params){
    return this.http.post(`${this.apiUrl2}/getChargingStationsByUserRoleAndLatLongUW/${login_id}`,params);
  }
 
  

  
}
