import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EndUserMapComponent } from './map-screen/end-user-map.component';

const routes : Routes = [
    {
        path : 'map',component : EndUserMapComponent
    }   
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class EndUserMapRoutingModule{}